import { Component, OnInit } from '@angular/core';
import { AngularFirestoreDocument, DocumentData } from '@angular/fire/firestore';
import { ActivatedRoute, Router } from '@angular/router';  
import { ChannelService } from 'src/app/services/channel.service';
import { SubscriptionService } from 'src/app/services/subscription.service';
import { UserService } from 'src/app/services/user.service';
import { AlertController, LoadingController, ToastController } from '@ionic/angular';
import { AccountService } from 'src/app/services/account.service';
import { Location } from '@angular/common';

@Component({
  selector: 'app-subscribers',
  templateUrl: './subscribers.page.html',
  styleUrls: ['./subscribers.page.scss'],
})
export class SubscribersPage implements OnInit {
  cid: string;
  subscribers: any[];
  showed_list: boolean = true
  start_after_docs: DocumentData[] =[];
  noNext: boolean   = false;
  noBefor: boolean  = true;  
  limit: number     = 2
  current_page_num  = 0;

  constructor(public route             : ActivatedRoute,
              public accountService    : AccountService,
              public alertController   : AlertController,
              public subscription      : SubscriptionService,
              public router            : Router,
              public location          : Location,
              public toastController   : ToastController,
              public userService       : UserService,
              public channelService    : ChannelService,
              public loadingController : LoadingController,
              ) { }

  ngOnInit() {
    this.cid   = this.route.snapshot.paramMap.get('cid')
    this.getSubscribers('next')
  }

  async getSubscribers(cmd:string){
    
    if(cmd == 'next' &&  !this.noNext){
      this.current_page_num++;
    }else if(cmd == 'prev' && !this.noBefor){
      this.current_page_num--;
      this.noNext = false
    }
     
    this.noBefor  = (this.current_page_num-1)?false:true
    let start_doc = this.start_after_docs[this.current_page_num-1]?this.start_after_docs[this.current_page_num-1]:null
    const loading     = await this.loadingController.create({ 
      message: 'Please wait...',
      duration: 5000
    });
    await loading.present();


    if(start_doc){ 
      this.channelService.getSubscribers(this.cid)
          .orderBy('created_at','desc') 
          .where('veryfied','==',this.showed_list)
          .startAfter(start_doc)
          .limit(this.limit)
          .get().then(async (sbscrb)=>{
        await loading.dismiss();
        this.subscribers = sbscrb.docs.map(doc=>{
          return doc.data();
        })
        this.getUserProfileImage();
        this.start_after_docs[this.current_page_num] = sbscrb.docs[sbscrb.docs.length-1]; 
        if(sbscrb.docs.length < this.limit){
          this.noNext = true
        }
      })
    }else{//end if
      this.channelService.getSubscribers(this.cid)
          .where('veryfied','==',this.showed_list)
          .orderBy('created_at','desc')
          .limit(this.limit)
          .get().then(async (sbscrb)=>{
        await loading.dismiss();
        this.subscribers = sbscrb.docs.map(doc=>{
          return doc.data();
        })
        this.getUserProfileImage();
        this.start_after_docs[this.current_page_num] = sbscrb.docs[sbscrb.docs.length-1]; 
        if(sbscrb.docs.length < this.limit){
          this.noNext = true
        }
      })
    }//end else


  }

  switchList(showed_list:boolean){ 
    this.showed_list      = showed_list;
    this.current_page_num = 0;
    this.start_after_docs = [];
    this.noNext           = false;
    this.noBefor          = true;
    this.getSubscribers('next')
  }

  async unSubscribe(subscriber:any){
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class', 
      message: 'Abonolik işlemini iptal ediyorsunuz ?',
      buttons: [{
          text: 'Tamam',
          handler: async () => {
            this.userService.unsubscribeFromChannel(this.cid,subscriber.uid).then(async ()=>{
              const toast = await this.toastController.create({
                color: 'dark',
                message: "Abonelik sonlandırıldı",
                duration: 1000
              });
              toast.present();
              this.subscribers = this.subscribers.filter(sbsc=>{
                if (sbsc != subscriber){
                  return sbsc;
                }
              })
            })
          }
        }, {
          text: 'Vazgeç',
          handler: async () => {}
      }]
    });
    await alert.present(); 
  }

  verify(subscriber:any){ 
    this.subscription.verify(this.cid,subscriber.uid).then(async (res)=>{
      const toast = await this.toastController.create({
        color: 'success',
        message: "Abonelik onaylandı",
        duration: 1000
      });
      toast.present();
      subscriber.veryfied = true;
      this.subscribers = this.subscribers.filter(sbsc=>{
        if (sbsc != subscriber){
          return sbsc;
        }
      })
    })
  }

  getUserProfileImage(){ 
    this.subscribers.map((subscriber)=>{ 
      this.accountService.getAccount(subscriber.uid).then((data)=>{ 
        subscriber['photoURL'] =  data.photoURL;
      });
    })
  }

  back(){
    this.location.back();
  }

  
}
